import React from 'react';
import { Tab, Row, Col, Nav, Dropdown, Table, Card, Image } from 'react-bootstrap';
import {db} from '../../services/firestore.js';
import Editmgmodal from '../ev-intelligence/mafia-gang/Editmgmodal.js';
import Infomgmodal from '../ev-intelligence/mafia-gang/Infomgmodal.js';
import Createmgmodal from '../ev-intelligence/mafia-gang/Createmgmodal.js';
import Deletemgmodal from '../ev-intelligence/mafia-gang/Deletemgmodal.js';

import Createobmodal from '../ev-intelligence/objetive/Createobmodal.js';
import Infoobmodal from '../ev-intelligence/objetive/Infoobmodal.js';
import Editobmodal from '../ev-intelligence/objetive/Editobmodal.js';
import Deleteobmodal from '../ev-intelligence/objetive/Deleteobmodal.js';

import Infocmmodal from '../ev-intelligence/companies/Infocmmodal.js';
import Createcmmodal from '../ev-intelligence/companies/Createcmmodal.js';
import Editcmmodal from '../ev-intelligence/companies/Editcmmodal.js';
import Deletecmmodal from '../ev-intelligence/companies/Deletecmmodal.js';

import Createcabmodal from '../ev-intelligence/cabines/Createcabmodal.js';
import Infocabmodal from '../ev-intelligence/cabines/Infocabmodal.js';
import Editcabmodal from '../ev-intelligence/cabines/Editcabmodal.js';
import Deletecabmodal from '../ev-intelligence/cabines/Deletecabmodal.js';

import Createstmodal from '../ev-operations/states/Createstmodal.js'
import Infostmodal from '../ev-operations/states/Infostmodal.js'
import Editstmodal from '../ev-operations/states/Editstmodal.js'
import Deletestmodal from '../ev-operations/states/Deletestmodal.js'

import Createdrmodal from '../ev-intendence/drugs/Createdrmodal.js'
import Editdrmodal from '../ev-intendence/drugs/Editdrmodal.js'
import Infodrmodal from '../ev-intendence/drugs/Infodrmodal.js'
import Deletedrmodal from '../ev-intendence/drugs/Deletedrmodal.js'

import Creategumodal from '../ev-intendence/guns/Creategumodal.js'
import Editgumodal from '../ev-intendence/guns/Editgumodal.js'
import Infogumodal from '../ev-intendence/guns/Infogumodal.js'
import Deletegumodal from '../ev-intendence/guns/Deletegumodal.js'
import './Evmenu.css';

class Evmenu extends React.Component{
    constructor(props){
        super(props);
        this.state = {
           'mg': [],
           'ob': [],
           'comp': [],
           'cabines': [],
           'states': [],
           'drugs': [],
           'guns': [],
           'departament': '',
           'userName': '',
           'grade': '',
           'tabDefault': ''
        }
    }

    componentDidMount(){
        // get the whole collection
        db.collection("mafia_gang").orderBy('name')
        .get()
        .then(querySnapshot => {
            var data = querySnapshot.docs.map(doc => doc.data());
            
            this.setState({
                'mg': data
            })
        });

        db.collection("objectives").orderBy('name')
        .get()
        .then(querySnapshot => {
            var data = querySnapshot.docs.map(doc => doc.data());

            this.setState({
                'ob': data
            })
        });

        db.collection("companies").orderBy('name')
        .get()
        .then(querySnapshot => {
            var data = querySnapshot.docs.map(doc => doc.data());

            this.setState({
                'comp': data
            })
        });

        db.collection("cabines").orderBy('id')
        .get()
        .then(querySnapshot => {
            var data = querySnapshot.docs.map(doc => doc.data());

            this.setState({
                'cabines': data
            })
        });

        db.collection("drugs").orderBy('name')
        .get()
        .then(querySnapshot => {
            var data = querySnapshot.docs.map(doc => doc.data());

            this.setState({
                'drugs': data
            })
        });

        db.collection("guns").orderBy('name')
        .get()
        .then(querySnapshot => {
            var data = querySnapshot.docs.map(doc => doc.data());

            this.setState({
                'guns': data
            })
        });

        db.collection("estates").orderBy('departament')
        .get()
        .then(querySnapshot => {
            var data = querySnapshot.docs.map(doc => doc.data());

            this.setState({
                'states': data
            })
        });

        var departament = localStorage.getItem('departamentUser');
        var user = localStorage.getItem('userName');
        var grade = localStorage.getItem('grade');
        var tabDefault = localStorage.getItem('tabDefault');

        this.setState({
            'departament': departament,
            'userName': user,
            'grade': grade,
            'tabDefault': tabDefault
        });        
    }

    render(){
        if(this.state.mg){
            const datostablaMg = this.state.mg;
            const datostablaOb = this.state.ob;
            const datostablaComp = this.state.comp;
            const datostablaSt = this.state.states;
            const datostablaCabines = this.state.cabines;
            const datostablaDrugs = this.state.drugs;
            const datostablaGuns = this.state.guns;
            const departament = this.state.departament;
            const userName = this.state.userName;
            const grade = this.state.grade;

            return (
                <div>
                    <Tab.Container id="left-tabs-example" defaultActiveKey='index'>
                    <Row>
                    <Col sm={3} className="bg-light">
                        {/* Imagen del menu */}
                        <div className="text-center pt-3">
                            <svg width="3em" height="3em" viewBox="0 0 16 16" className="bi bi-person-circle" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                <path d="M13.468 12.37C12.758 11.226 11.195 10 8 10s-4.757 1.225-5.468 2.37A6.987 6.987 0 0 0 8 15a6.987 6.987 0 0 0 5.468-2.63z"/>
                                <path fillRule="evenodd" d="M8 9a3 3 0 1 0 0-6 3 3 0 0 0 0 6z"/>
                                <path fillRule="evenodd" d="M8 1a7 7 0 1 0 0 14A7 7 0 0 0 8 1zM0 8a8 8 0 1 1 16 0A8 8 0 0 1 0 8z"/>
                            </svg><br></br>
                                <strong>{userName}</strong>
                        </div>
                        {/* <Image src="./../img/reportada.jpg" roundedCircle/> */}
                        <br></br>
                        {/* Opciones del menu */}
                        <Nav variant="tabs" className="flex-column">
                            <Nav.Item>
                                <Nav.Link eventKey='index' className="text-center" >Informacin</Nav.Link>
                            </Nav.Item>
                            <h5 className="mb-1 text-dark">Inteligencia</h5>
                            <Nav.Item>
                                {departament === 'Inteligencia Legal' ? <Nav.Link disabled>Elemento Oculto</Nav.Link> : <Nav.Link eventKey="mafia-gang">Mafias / Bandas</Nav.Link>}
                            </Nav.Item>
                            <Nav.Item>
                                {departament === 'Inteligencia Legal' ? <Nav.Link disabled>Elemento Oculto</Nav.Link> : <Nav.Link eventKey="objectives">Objetivos</Nav.Link>}
                            </Nav.Item>
                            <Nav.Item>
                                {departament === 'Inteligencia Legal' ? <Nav.Link disabled>Elemento Oculto</Nav.Link> : <Nav.Link eventKey="cabines">Cabinas</Nav.Link>}
                            </Nav.Item>
                            <Nav.Item>
                                <Nav.Link eventKey="companies">Empresas</Nav.Link>
                            </Nav.Item>
                            <Dropdown.Divider />
                            <h5 className="mb-1 text-dark">Intendencia</h5>                
                            <Nav.Item>
                                {departament === 'Inteligencia Legal' ? <Nav.Link disabled>Elemento Oculto</Nav.Link> : <Nav.Link eventKey="drugs">Drogas</Nav.Link>}
                            </Nav.Item>
                            <Nav.Item>
                                {departament === 'Inteligencia Legal' ? <Nav.Link disabled>Elemento Oculto</Nav.Link> : <Nav.Link eventKey="guns">Armas</Nav.Link>}
                            </Nav.Item>
                                        
                            <Dropdown.Divider />
                            <h5 className="mb-1 text-dark">Operaciones</h5>
                            <Nav.Item>
                                {departament === 'Inteligencia Legal' ? <Nav.Link disabled>Elemento Oculto</Nav.Link> : <Nav.Link eventKey="estates">Propiedades</Nav.Link>}
                            </Nav.Item>
                            <Nav.Item>
                                {departament === 'Inteligencia Legal' ? <Nav.Link disabled>Elemento Oculto</Nav.Link> : <Nav.Link eventKey="vehicles">Vehculos</Nav.Link>}
                            </Nav.Item>
                        </Nav>                    
                    </Col>
                    <Col sm={9} className="p-0 m-0">
                        <Image src="https://i.imgur.com/ebeXKVX.jpg" width="100%"></Image> 

                        <Tab.Content>
                        <Tab.Pane eventKey="mafia-gang">
                            {/* Botn de crear mafia */}
                            <p className="text-right m-2">
                                <Createmgmodal datos={datostablaMg}></Createmgmodal>
                            </p>

                            {/* Tabla de las mafias */}
                            <Table bordered hover className="text-center">
                                <thead>
                                    <tr className="text-center thead-dark">
                                        <th>Nombre</th>
                                        <th>Lder</th>
                                        <th>N Miembros Conocidos</th>
                                        <th>Armas</th>
                                        <th>Drogas</th>
                                        <th>Relacin</th>
                                        <th>Editar/Eliminar</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {datostablaMg.map((listValue, index) => {
                                        var color;
                                        
                                        if(listValue.relationship === 'Amiga')
                                            color = 'table-success';
                                        
                                        if (listValue.relationship === 'Enemiga')
                                            color = 'table-danger';

                                        return(
                                            <tr key={index} className={color}>
                                                <td><b>{listValue.name}</b> <Infomgmodal datos={datostablaMg[index]}></Infomgmodal></td>
                                                <td>{listValue.leader ? listValue.leader : 'No conocido'}</td>
                                                <td>{listValue.n_members ? listValue.n_members : 'No conocido'}</td>
                                                <td>{listValue.guns_yn ? 'S' : 'No / No conocido'}</td>
                                                <td>{listValue.drugs_yn ? 'S' : 'No / No conocido'}</td>
                                                <td>{listValue.relationship}</td>
                                                <td>
                                                    <Editmgmodal datos={datostablaMg[index]}></Editmgmodal>
                                                    <Deletemgmodal datos={datostablaMg[index].name}></Deletemgmodal>
                                                </td>
                                            </tr>
                                        );
                                    })}
                                </tbody>
                            </Table>
                        </Tab.Pane>
                        <Tab.Pane eventKey="objectives">
                            {/* Botn de crear Objetivo */}
                            <p className="text-right m-2">
                                <Createobmodal datos={datostablaOb}></Createobmodal>
                            </p>

                            {/* Tabla de los objetivos */}
                            <Table bordered hover className="text-center">
                                <thead>
                                    <tr className="text-center thead-dark">
                                        <th>Nombre</th>
                                        <th>Mafia o Banda</th>
                                        <th>Vehculo</th>
                                        <th>Actitud</th>                                        
                                        <th>Relacin</th>
                                        <th>Prioridad</th>                                        
                                        <th>Editar/Eliminar</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {datostablaOb.map((listValue, index) => {
                                        var color;
                                        
                                        if(listValue.relationship === 'Amiga')
                                            color = 'table-success';
                                        
                                        if (listValue.relationship === 'Enemiga')
                                            color = 'table-danger';

                                        return(
                                            <tr key={index} className={color}>
                                                <td><b>{listValue.name}</b> <Infoobmodal datos={datostablaOb[index]}></Infoobmodal></td>
                                                <td>{listValue.mg ? listValue.mg : 'No conocido'}</td>
                                                <td>{listValue.vehicle_model ? listValue.vehicle_model : 'No / No conocido'}</td> 
                                                <td className="text-center">
                                                    {(listValue.attitude === 'Pacfica') ? <svg width="2em" height="2em" viewBox="0 0 16 16" className="bi bi-emoji-laughing-fill text-success" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                                                                                <path fillRule="evenodd" d="M8 16A8 8 0 1 0 8 0a8 8 0 0 0 0 16zM7 6.5c0 .501-.164.396-.415.235C6.42 6.629 6.218 6.5 6 6.5c-.218 0-.42.13-.585.235C5.164 6.896 5 7 5 6.5 5 5.672 5.448 5 6 5s1 .672 1 1.5zm5.331 3a1 1 0 0 1 0 1A4.998 4.998 0 0 1 8 13a4.998 4.998 0 0 1-4.33-2.5A1 1 0 0 1 4.535 9h6.93a1 1 0 0 1 .866.5zm-1.746-2.765C10.836 6.896 11 7 11 6.5c0-.828-.448-1.5-1-1.5s-1 .672-1 1.5c0 .501.164.396.415.235.165-.106.367-.235.585-.235.218 0 .42.13.585.235z"/>
                                                                                            </svg> : ''}
                                                    {(listValue.attitude === 'Agresiva') ? <svg width="2em" height="2em" viewBox="0 0 16 16" className="bi bi-emoji-angry-fill text-danger" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                                                                                <path fillRule="evenodd" d="M8 16A8 8 0 1 0 8 0a8 8 0 0 0 0 16zM4.053 4.276a.5.5 0 0 1 .67-.223l2 1a.5.5 0 0 1 .166.76c.071.206.111.44.111.687C7 7.328 6.552 8 6 8s-1-.672-1-1.5c0-.408.109-.778.285-1.049l-1.009-.504a.5.5 0 0 1-.223-.67zm.232 8.157a.5.5 0 0 0 .683-.183A3.498 3.498 0 0 1 8 10.5c1.295 0 2.426.703 3.032 1.75a.5.5 0 1 0 .866-.5A4.498 4.498 0 0 0 8 9.5a4.5 4.5 0 0 0-3.898 2.25.5.5 0 0 0 .183.683zM10 8c.552 0 1-.672 1-1.5 0-.408-.109-.778-.285-1.049l1.009-.504a.5.5 0 1 0-.448-.894l-2 1a.5.5 0 0 0-.165.76A2.12 2.12 0 0 0 9 6.5c0 .828.448 1.5 1 1.5z"/>
                                                                                            </svg> : ''}
                                                    {(listValue.attitude === 'Neutral') ? <svg width="2em" height="2em" viewBox="0 0 16 16" className="bi bi-emoji-neutral-fill" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                                                                            <path fillRule="evenodd" d="M8 16A8 8 0 1 0 8 0a8 8 0 0 0 0 16zM7 6.5C7 7.328 6.552 8 6 8s-1-.672-1-1.5S5.448 5 6 5s1 .672 1 1.5zm-3 4a.5.5 0 0 1 .5-.5h7a.5.5 0 0 1 0 1h-7a.5.5 0 0 1-.5-.5zM10 8c.552 0 1-.672 1-1.5S10.552 5 10 5s-1 .672-1 1.5S9.448 8 10 8z"/>
                                                                                        </svg> : ''}
                                                </td>
                                                <td>{listValue.relationship}</td>
                                                <td>{listValue.priority ? listValue.priority : 'No / No conocido'}</td>
                                                <td>
                                                    <Editobmodal datos={datostablaOb[index]}></Editobmodal>
                                                    <Deleteobmodal datos={datostablaOb[index].name}></Deleteobmodal>
                                                </td>
                                            </tr>
                                        );
                                    })}
                                </tbody>
                            </Table>
                        </Tab.Pane>
                        <Tab.Pane eventKey="companies">
                            {/* Botn de crear Compaia */}
                            <p className="text-right m-2">
                                <Createcmmodal datos={datostablaComp}></Createcmmodal>
                            </p>

                            {/* Tabla de los objetivos */}
                            <Table bordered hover className="text-center">
                                <thead>
                                    <tr className="text-center thead-dark">
                                        <th>Empresa</th>
                                        <th>Contacto</th>
                                        <th>Telfono</th>
                                        <th>Pagos</th>                                        
                                        <th>Recursos</th>
                                        <th>Editar/Eliminar</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {datostablaComp.map((listValue, index) => {
                                        var color;
                                        
                                        // if(listValue.payment === 'Al da')
                                        //     color = 'table-success';
                                        
                                        // if (listValue.payment === 'No pagado')
                                        //     color = 'table-danger';

                                        return(
                                            <tr key={index} className={color}>
                                                <td><b>{listValue.name}</b> <Infocmmodal datos={datostablaComp[index]}></Infocmmodal></td>
                                                <td>{listValue.contact ? listValue.contact : 'No conocido'}</td>
                                                <td>{listValue.phone ? listValue.phone : 'No conocido'}</td> 
                                                <td className="text-center">
                                                    {(listValue.payment === 'Al da') ? <svg width="1em" height="1em" viewBox="0 0 16 16" className="bi bi-credit-card-fill text-success" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                                                                            <path d="M0 4a2 2 0 0 1 2-2h12a2 2 0 0 1 2 2v1H0V4z"/>
                                                                                            <path fillRule="evenodd" d="M0 7v5a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V7H0zm3 2a1 1 0 0 0-1 1v1a1 1 0 0 0 1 1h1a1 1 0 0 0 1-1v-1a1 1 0 0 0-1-1H3z"/>
                                                                                        </svg> : ''}
                                                    {(listValue.payment === 'No pagado') ? <svg width="1em" height="1em" viewBox="0 0 16 16" className="bi bi-credit-card-fill text-danger" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                                                                                <path d="M0 4a2 2 0 0 1 2-2h12a2 2 0 0 1 2 2v1H0V4z"/>
                                                                                                <path fillRule="evenodd" d="M0 7v5a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V7H0zm3 2a1 1 0 0 0-1 1v1a1 1 0 0 0 1 1h1a1 1 0 0 0 1-1v-1a1 1 0 0 0-1-1H3z"/>
                                                                                            </svg> : ''}
                                                </td>
                                                <td>{(listValue.resources) ? 'S' : 'No'}</td>
                                                <td>
                                                    <Editcmmodal datos={datostablaComp[index]}></Editcmmodal>
                                                    <Deletecmmodal datos={datostablaComp[index].name}></Deletecmmodal>
                                                </td>
                                            </tr>
                                        );
                                    })}
                                </tbody>
                            </Table>
                        </Tab.Pane>
                        <Tab.Pane eventKey="cabines">
                            {/* Botn de crear Cabina */}
                            <p className="text-right m-2">
                                <Createcabmodal datos={datostablaComp}></Createcabmodal>
                            </p>

                            {/* Tabla de los objetivos */}
                            <Table bordered hover className="text-center">
                                <thead>
                                    <tr className="text-center thead-dark">
                                        <th>Id</th>
                                        <th>Calle</th>
                                        <th>Cruce</th>
                                        <th>Nmeros</th>
                                        <th>Imagen Mapa</th>
                                        <th>Editar/Eliminar</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {datostablaCabines.map((listValue, index) => {
                                        var color;
                                        
                                        // if(listValue.payment === 'Al da')
                                        //     color = 'table-success';
                                        
                                        // if (listValue.payment === 'No pagado')
                                        //     color = 'table-danger';

                                        return(
                                            <tr key={index} className={color}>
                                                <td><b className="mx-3">{listValue.id}</b><Infocabmodal datos={datostablaCabines[index]}></Infocabmodal></td>
                                                <td>{listValue.street ? listValue.street : 'No conocido'}</td>
                                                <td>{listValue.crossing ? listValue.crossing : 'No conocido'}</td> 
                                                <td>{(listValue.cabine_number) ? listValue.cabine_number : 'No'}</td>
                                                <td>{(listValue.cabine_pic) ? 'S' : 'No'}</td>
                                                <td>
                                                    <Editcabmodal datos={datostablaCabines[index]}></Editcabmodal>
                                                    <Deletecabmodal datos={datostablaCabines[index]}></Deletecabmodal>
                                                </td>
                                            </tr>
                                        );
                                    })}
                                </tbody>
                            </Table>
                        </Tab.Pane>
                        <Tab.Pane eventKey="drugs">
                            {/* Botn de crear Droga */}
                            <p className="text-right m-2">
                                <Createdrmodal datos={datostablaDrugs}></Createdrmodal>
                            </p>

                            {/* Tabla de los objetivos */}
                            <Table bordered hover className="text-center">
                                <thead>
                                    <tr className="text-center thead-dark">
                                        <th>Nombre</th>
                                        <th>Cantidad</th>
                                        <th>Precio Proveedor</th>
                                        <th>Precio Coste Unidad</th>
                                        <th>Precio Venta Unidad</th>                                        
                                        <th>Editar/Eliminar</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {datostablaDrugs.map((listValue, index) => {
                                        var color;
                                        
                                        return(
                                            <tr key={index} className={color}>
                                                <td><b className="mx-3">{listValue.name}</b><Infodrmodal datos={datostablaDrugs[index]}></Infodrmodal></td>
                                                <td>{listValue.quantity ? listValue.quantity : 'No conocido'}</td>
                                                <td>{listValue.supplier_price ? listValue.supplier_price : 'No conocido'}</td> 
                                                <td>{(listValue.cost_price) ? listValue.cost_price : 'No conocido.'}</td>
                                                <td>{(listValue.sale_price) ? listValue.sale_price : 'No conocido.'}</td>
                                                <td>
                                                    <Editdrmodal datos={datostablaDrugs[index]}></Editdrmodal>
                                                    <Deletedrmodal datos={datostablaDrugs[index]}></Deletedrmodal>
                                                </td>
                                            </tr>
                                        );
                                    })}
                                </tbody>
                            </Table>
                        </Tab.Pane>
                        <Tab.Pane eventKey="guns">
                            {/* Botn de crear Arma */}
                            <p className="text-right m-2">
                                <Creategumodal datos={datostablaGuns}></Creategumodal>
                            </p>

                            {/* Tabla de los objetivos */}
                            <Table bordered hover className="text-center">
                                <thead>
                                    <tr className="text-center thead-dark">
                                        <th>Nombre</th>
                                        <th>Cantidad</th>
                                        <th>Precio Proveedor</th>
                                        <th>Precio Coste Unidad</th>
                                        <th>Precio Venta Unidad</th>                                        
                                        <th>Editar/Eliminar</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {datostablaGuns.map((listValue, index) => {
                                        var color;
                                        
                                        return(
                                            <tr key={index} className={color}>
                                                <td><b className="mx-3">{listValue.name}</b><Infogumodal datos={datostablaGuns[index]}></Infogumodal></td>
                                                <td>{listValue.quantity ? listValue.quantity : 'No conocido'}</td>
                                                <td>{listValue.supplier_price ? listValue.supplier_price : 'No conocido'}</td> 
                                                <td>{(listValue.cost_price) ? listValue.cost_price : 'No conocido.'}</td>
                                                <td>{(listValue.sale_price) ? listValue.sale_price : 'No conocido.'}</td>
                                                <td>
                                                    <Editgumodal datos={datostablaGuns[index]}></Editgumodal>
                                                    <Deletegumodal datos={datostablaGuns[index]}></Deletegumodal>
                                                </td>
                                            </tr>
                                        );
                                    })}
                                </tbody>
                            </Table>
                        </Tab.Pane>
                        <Tab.Pane eventKey="estates">
                            {/* Botn de crear Compaia */}
                            <p className="text-right m-2">
                                <Createstmodal datos={datostablaSt}></Createstmodal>
                            </p>

                            {/* Tabla de los objetivos */}
                            <Table bordered hover className="text-center">
                                <thead>
                                    <tr className="text-center thead-dark">
                                        <th>Nombre</th>
                                        <th>Propietario</th>
                                        <th>Llave</th>
                                        <th>ltima Apertura</th>
                                        <th>Departamento</th>
                                        <th>Editar/Eliminar</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {datostablaSt.map((listValue, index) => {
                                        var color = 'table-light';
                                        if(Date.parse(listValue.last_opened)){
                                            var ultimoDia = new Date(Date.parse(listValue.last_opened));
                                            var hoy = new Date(Date.now());

                                            var last_month = ultimoDia.getMonth();
                                            var now_month = hoy.getMonth();

                                            var last_day = ultimoDia.getDate();
                                            var now_day = hoy.getDate();

                                            if (now_month < last_month){
                                                if(now_day < 3){
                                                    color = 'table-warning'
                                                }else{
                                                    color = 'table-danger'
                                                }
                                            }else if(now_day > last_day + 4){
                                                color = 'table-danger'
                                            }else if (now_day > last_day + 2){
                                                color = 'table-warning'
                                            }
                                        }

                                        return(
                                            <tr key={index} className={color}>
                                                <td><b>{listValue.name}</b> <Infostmodal datos={datostablaSt[index]}></Infostmodal></td>
                                                <td>{listValue.owner ? listValue.owner : 'No conocido'}</td>
                                                <td>{listValue.key ? listValue.key : 'No conocido'}</td>
                                                <td className={color}>{listValue.last_opened ? listValue.last_opened : 'No conocido'}</td>
                                                <td className="text-center">
                                                    {(listValue.departament === 'Inteligencia') ? <svg width="1.5em" height="1.5em" viewBox="0 0 16 16" className="bi bi-binoculars-fill text-info" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                                                                                <path d="M4.5 1A1.5 1.5 0 0 0 3 2.5V3h4v-.5A1.5 1.5 0 0 0 5.5 1h-1zM7 4v1h2V4h4v.882a.5.5 0 0 0 .276.447l.895.447A1.5 1.5 0 0 1 15 7.118V13H9v-1.5a.5.5 0 0 1 .146-.354l.854-.853V9.5a.5.5 0 0 0-.5-.5h-3a.5.5 0 0 0-.5.5v.793l.854.853A.5.5 0 0 1 7 11.5V13H1V7.118a1.5 1.5 0 0 1 .83-1.342l.894-.447A.5.5 0 0 0 3 4.882V4h4zM1 14v.5A1.5 1.5 0 0 0 2.5 16h3A1.5 1.5 0 0 0 7 14.5V14H1zm8 0v.5a1.5 1.5 0 0 0 1.5 1.5h3a1.5 1.5 0 0 0 1.5-1.5V14H9zm4-11H9v-.5A1.5 1.5 0 0 1 10.5 1h1A1.5 1.5 0 0 1 13 2.5V3z"/>
                                                                                            </svg> : ''}
                                                    {(listValue.departament === 'Intendencia') ? <svg width="1.5em" height="1.5em" viewBox="0 0 16 16" className="bi bi-book-half text-success" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                                                                                <path fillRule="evenodd" d="M8.5 2.687v9.746c.935-.53 2.12-.603 3.213-.493 1.18.12 2.37.461 3.287.811V2.828c-.885-.37-2.154-.769-3.388-.893-1.33-.134-2.458.063-3.112.752zM8 1.783C7.015.936 5.587.81 4.287.94c-1.514.153-3.042.672-3.994 1.105A.5.5 0 0 0 0 2.5v11a.5.5 0 0 0 .707.455c.882-.4 2.303-.881 3.68-1.02 1.409-.142 2.59.087 3.223.877a.5.5 0 0 0 .78 0c.633-.79 1.814-1.019 3.222-.877 1.378.139 2.8.62 3.681 1.02A.5.5 0 0 0 16 13.5v-11a.5.5 0 0 0-.293-.455c-.952-.433-2.48-.952-3.994-1.105C10.413.809 8.985.936 8 1.783z"/>
                                                                                            </svg> : ''}
                                                    {(listValue.departament === 'Operaciones') ? <svg width="1.5em" height="1.5em" viewBox="0 0 16 16" className="bi bi-shield-shaded text-danger" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                                                                                    <path fillRule="evenodd" d="M5.443 1.991a60.17 60.17 0 0 0-2.725.802.454.454 0 0 0-.315.366C1.87 7.056 3.1 9.9 4.567 11.773c.736.94 1.533 1.636 2.197 2.093.333.228.626.394.857.5.116.053.21.089.282.11A.73.73 0 0 0 8 14.5c.007-.001.038-.005.097-.023.072-.022.166-.058.282-.111.23-.106.525-.272.857-.5a10.197 10.197 0 0 0 2.197-2.093C12.9 9.9 14.13 7.056 13.597 3.159a.454.454 0 0 0-.315-.366c-.626-.2-1.682-.526-2.725-.802C9.491 1.71 8.51 1.5 8 1.5c-.51 0-1.49.21-2.557.491zm-.256-.966C6.23.749 7.337.5 8 .5c.662 0 1.77.249 2.813.525a61.09 61.09 0 0 1 2.772.815c.528.168.926.623 1.003 1.184.573 4.197-.756 7.307-2.367 9.365a11.191 11.191 0 0 1-2.418 2.3 6.942 6.942 0 0 1-1.007.586c-.27.124-.558.225-.796.225s-.526-.101-.796-.225a6.908 6.908 0 0 1-1.007-.586 11.192 11.192 0 0 1-2.417-2.3C2.167 10.331.839 7.221 1.412 3.024A1.454 1.454 0 0 1 2.415 1.84a61.11 61.11 0 0 1 2.772-.815z"/>
                                                                                                    <path d="M8 2.25c.909 0 3.188.685 4.254 1.022a.94.94 0 0 1 .656.773c.814 6.424-4.13 9.452-4.91 9.452V2.25z"/>
                                                                                                </svg> : ''}
                                                    {(listValue.departament === 'Personal') ? <svg width="1.5em" height="1.5em" viewBox="0 0 16 16" className="bi bi-door-open-fill" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                                                                                <path fillRule="evenodd" d="M1.5 15a.5.5 0 0 0 0 1h13a.5.5 0 0 0 0-1H13V2.5A1.5 1.5 0 0 0 11.5 1H11V.5a.5.5 0 0 0-.57-.495l-7 1A.5.5 0 0 0 3 1.5V15H1.5zM11 2v13h1V2.5a.5.5 0 0 0-.5-.5H11zm-2.5 8c-.276 0-.5-.448-.5-1s.224-1 .5-1 .5.448.5 1-.224 1-.5 1z"/>
                                                                                            </svg> : ''}
                                                    {(listValue.departament === 'X-Negocios') ? <svg width="1.5em" height="1.5em" viewBox="0 0 16 16" className="bi bi-cash-stack text-warning" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                                                                                    <path d="M14 3H1a1 1 0 0 1 1-1h12a1 1 0 0 1 1 1h-1z"/>
                                                                                                    <path fillRule="evenodd" d="M15 5H1v8h14V5zM1 4a1 1 0 0 0-1 1v8a1 1 0 0 0 1 1h14a1 1 0 0 0 1-1V5a1 1 0 0 0-1-1H1z"/>
                                                                                                    <path d="M13 5a2 2 0 0 0 2 2V5h-2zM3 5a2 2 0 0 1-2 2V5h2zm10 8a2 2 0 0 1 2-2v2h-2zM3 13a2 2 0 0 0-2-2v2h2zm7-4a2 2 0 1 1-4 0 2 2 0 0 1 4 0z"/>
                                                                                                </svg> : ''}
                                                </td>
                                                <td>
                                                    <Editstmodal datos={datostablaSt[index]}></Editstmodal>
                                                    <Deletestmodal datos={datostablaSt[index]}></Deletestmodal>
                                                </td>
                                            </tr>
                                        );
                                    })}
                                </tbody>
                            </Table>
                        </Tab.Pane>
                        <Tab.Pane eventKey="vehicles">
                            <Card style={{ width: '18rem' }}>
                                <Card.Img variant="top" src="https://media.giphy.com/media/26tn33aiTi1jkl6H6/giphy.gif" />
                                <Card.Body>
                                    <Card.Title>Programando Vehculos...</Card.Title>
                                    <Card.Text>
                                        Esta seccin est an en proceso. Por favor, ten paciencia. Solo soy una programadora.
                                    </Card.Text>
                                </Card.Body>
                            </Card>
                        </Tab.Pane>
                        <Tab.Pane eventKey="index" className="text-center pt-5 px-1">
                            <h3> Informacin Confidencial </h3>
                            <p>La distribucin de esta informacin o parte de ella, fuera de los componentes de esta organizacin conlleva
                                {departament === 'Inteligencia Legal' ? <div className="text-danger font-weight-bold">el despido inmediato.</div> : <div className="text-danger font-weight-bold">la muerte.</div>}
                            </p>
                            {/* <iframe title="videoEmbed" width="560" height="315" src="https://www.youtube.com/embed/Exdt3upYpqA?controls=0" frameBorder="0" allow="accelerometer; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe> */}
                        </Tab.Pane>
                        </Tab.Content>
                    </Col>
                    </Row>
                    </Tab.Container>
                </div>
            );
        }else{
            return(
                <div></div>
            )
        }
    }
}

export default Evmenu;